* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Segoe UI';
  border: none;
  background: none
}

.container {
  display: grid;
  grid-template-rows: 55px 210px 80px 55px;
  grid-template-columns: 300px;
  justify-items: center;
  color: #707070;
  border: 1px solid #707070;
  -ms-flex-align: center;
      align-items: center;
}

.controls {
  display: grid;
  grid-template-columns: repeat(3, 1fr)
}

.count {
  font-size: 90px;
    border-bottom: 1px solid #707070;
    height: 100%;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.subgrid {
  font-size: 20px;
  border-bottom: 1px solid #707070;
  width: 100%;
  height: 100%;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

 button {
  cursor: pointer;
  height: 100%;
  width: 100%;
  border: none;
  border-top: 1px solid #707070;
  font-size: 20px;
  color: #707070

}
button:hover {
  background: #707070;
  color: white
}

.control_wrap {
  display: -ms-flexbox;
  display: flex;
}

.control_wrap > div {
  height: 100%;
}

.control_divider {
  width: 100px;
}
.control_divider:first-of-type{
  border-right: 1px solid #707070
}

.control_divider:last-of-type{
  border-left: 1px solid #707070
}

.control_divider > span {
  width: 100px;
  height: 25px;
  display: block;
  text-align: center;
  border-bottom: 1px solid #707070
}

.control_divider button {
  height: 55px;
}

.control_divider input {
 text-align: center;
  width: 100%;
  height: 55px;
  font-size: 20px;
  color: #707070
}

.control_warp > div:nth-child(1) {
  border: none 1px solid #707070
}


